<template>
    <form class="formulario" @submit.prevent="submitForm">
        <div class="mb-3">
            <div class="form-check d-flex justify-content-center">
            <label class="form-check-label text-primary " for="ingreso">INGRESO</label>
            <input class="form-check-input    ms-2" type="radio" id="ingreso" value="ingreso" name="tipoMovimiento" v-model="type" required>
            <label class="form-check-label text-danger ms-3" for="gasto">GASTO</label>
            <input class="form-check-input  ms-2" type="radio" id="gasto" name="tipoMovimiento" value="gasto" v-model="type" required>
            </div>
        </div>
        <div class="mb-3 valores">
            <label class="form-check-label mb-2">MONTO</label>
            <input type="number" id="disabledTextInput" class="form-control" placeholder="Ingresa cantidad..." v-model="amount" required>
        </div>
        <div class="mb-3 valores">
            <label class="form-check-label mb-2">Alguna Nota del Mov</label>
            <input type="text" id="disabledTextInput" class="form-control" placeholder="Nota aqu√≠..." v-model="nota" required>
        </div>
        <button type="submit" class="btn btn-primary mb-2">INGRESAR</button>

    </form>
</template>
    
    <script>
    export default {
        name: 'IngresoGastoForm',
        data() {
        return {
            type: '',
            amount: null,
            nota: '',
            fecha: String
        };
        },
        methods: {
        submitForm() {
            const entry = {
            type: this.type,
            amount: parseFloat(this.amount),
            nota: this.nota,
            fecha: new Date().toLocaleString('es-CO') 
            };
            this.$emit('addEntry', entry);
            this.type = '',
            this.amount = null,
            this.nota = '',
            this.fecha = ''
        }
        }
    };
    </script>

<style>
.formulario{
    margin-top: 60px;
}
.valores input{
    margin: auto;
    width: 80% ;
    box-shadow: -2px 2px 3px 2px black;
}
.valores label{
    margin-top: 20px;
}
</style>