    <template>
        <div class="grafica">
            <h2>{{ title }}</h2>
            <canvas ref="chartRef"></canvas>
        </div>
    </template>
    
    <script>
    import { ref, onMounted, watch } from 'vue';
    import Chart from 'chart.js/auto';
    
    export default {
        name: 'Chart',
        props: {
        entries: {
            type: Array,
            required: true
        },
        title: {
            type: String,
            required: true
        },
        color: {
            type: String,
            required: true
        }
        },
        setup(props) {
        const chartRef = ref(null);
        let myChart = null;
    
        onMounted(() => {
            const ctx = chartRef.value.getContext('2d');
            myChart = new Chart(ctx, {
            type: 'line',
            data: {
                labels: props.entries.map((_,index) => ` ${index + 1}`),
                datasets: [{
                label: props.title,
                data: props.entries.map(entry => entry.amount),
                backgroundColor: props.color,
                borderWidth: 7
                

                }],
                
            }
            });
        });
    
        watch(() => props.entries, () => {
            myChart.data.labels = props.entries.map((_, index) => ` ${index + 1}`);
            myChart.data.datasets[0].data = props.entries.map(entry => entry.amount);
            myChart.update();
        }, { deep: true });
    
        return {
            chartRef
        };
        }
    };
    </script>
    
<style>

@media(max-width:550px){
    .grafica{
    border: 1px solid black;
    max-width: 200px;
}

canvas{
    display: flex;
    flex-grow: 1;
    width: 100%;
    height: 900px;
}
}

    </style>
    