<template>
  <div id="app">
    <div class="headd">
      <div class="overlay"></div>
      <img class="logo" src="../img/th.jpeg" alt="logo">
      <h1 class="titulo">Finanzas a tu Alcance</h1>
  </div>
    <IngresoGastoForm @addEntry="addEntry" />
    
    <div class="charts">
      <Chart :entries="ingresos" title="Ingresos" color="#0d6efd" />
      <Chart :entries="gastos" title="Gastos" color="#f44336" />
    </div>
    <Shearch @buscar="buscar"/>
    <IngresoGastoList :entries="copyEntries"   @remove="Remove"/>
  </div> 
</template>

<script>
import IngresoGastoForm from './components/IngresoGastoForm.vue';
import IngresoGastoList from './components/IngresoGastoList.vue';
import Chart from './components/Chart.vue';
import Shearch from './components/Shearch.vue'


export default {
  name: 'App',
  components: {
    IngresoGastoForm,
    IngresoGastoList,
    Chart,
    Shearch
  },
  data() {
    return {
      entries: [],
      copyEntries: [],
      calcula: Boolean
    }
  },
  computed: {
    ingresos() {
      return this.copyEntries.filter(entry => entry.type === 'ingreso');
    },
    gastos() {
      return this.copyEntries.filter(entry => entry.type === 'gasto');
    }
  },
  methods: {
  addEntry(entry) {
    this.entries.push(entry);
    this.copyEntries = [...this.entries];
    this.updateLocalStorage();
  },
  Calcular(sing){
    this.calcula = sing;

  },
  Remove(index){
    this.entries.splice(index,1);
    this.copyEntries = [...this.entries];
    this.updateLocalStorage();
  },

  buscar(query){
    if(query.trim()===''){
      this.copyEntries = [...this.entries];
    }else{
      
      const temp = this.copyEntries.filter(entry=>{
        return entry.nota.toLowerCase().includes(query.toLowerCase());
      });
      this.copyEntries = [...temp];
    }
    this.updateLocalStorage();
  },
  updateLocalStorage(){
    localStorage.setItem('expense-traker', JSON.stringify(this.entries));
  }
},
mounted() {
  const savedTasks = JSON.parse(localStorage.getItem('expense-traker'));
  if (savedTasks) {
    this.entries = savedTasks;
    this.copyEntries = [...this.entries];
  }
}

}

</script>

