<template>
  <div>
      <hr>
      <button class="btn btn-warning mb-2" @click="changeState()">{{showInfo? BotonMagico : BotonMagico2 }}</button>
        <div v-if="showInfo">
          <div class="d-flex justify-content-around align-items-center mb-3">
              <h3>INGRESOS:üíµ{{ totalIngresos }}</h3>
              <h3>GASTOS:üíµ{{ totalGastos }}</h3>
          </div>
          <div class="mt-3" v-for="(mov, index) in entries" :key="index">
            
              <div >
                  <div  :class="['alert', mov.type==='ingreso' ? 'alert-primary' : 'alert-danger' , 'd-flex', 'justify-content-between']">
                      <div>
                          {{ mov.type }}:üíµ:{{ mov.amount }} üóíÔ∏è: "{{ mov.nota }}"  üìÖ:{{ mov.fecha }}
                      </div>
                      <div>
                          <button class="btn btn-danger btn-sm px-2" @click="removeEntry(index)">‚úÇÔ∏è</button>
                      </div>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'IngresoGastoList',
  props: {
    entries: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      showInfo: true,
      BotonMagico2: 'MOSTAR HISTORIAL',
      BotonMagico: 'OCULTAR'
    };
  },
  methods: {
    removeEntry(index) {
      this.$emit('remove', index);
    },
    changeState() {
      this.showInfo = !this.showInfo;
    }
  },
  computed: {
    totalIngresos() {
      return this.entries
        .filter(entry => entry.type === 'ingreso')
        .reduce((acc, entry) => acc + parseFloat(entry.amount), 0);
    },
    totalGastos() {
      return this.entries
        .filter(entry => entry.type === 'gasto')
        .reduce((acc, entry) => acc + parseFloat(entry.amount), 0);
    }
  }
};
</script>

<style scoped>
  /* ... */
</style>


